@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100vh;
  heigth: 100%;
  display: flex;
  color: #3c3c3c;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
}

.patient-info {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  margin: 10px;
  width: 100%;
}

.patient-info_content {
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.patient-info_content_inputContainer {
  margin: 10px 0;
}

.patient-info_content__input {
  border: 0;
  border-bottom: 1px solid #d9d9d9;
  width: 200px;
  font-size: 14px;
  color: #3c3c3c;
  padding-left: 5px;
}

.patient-info_header {
  background-color: #3398cc;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.patient-info_header__arrow {
  height: 16px;
  width: 16px;
}

.patients-list {
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 10px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

.patients-list_tabs {
  display: flex;
  border-bottom: 1px solid #d9d9d9;
  background-color: #fafafa;
}

.patients-list_tabs__tab {
  padding: 20px;
}

.patients-list_tabs__tab:hover {
  border-bottom: 3px solid #3398cc;
  padding-bottom: 2px;
  color: #3398cc;
  -webkit-transition: 100ms;
  transition: 100ms;
}

.patients-list_display {
  width: 100%;
  margin: 0;
  vertical-align: middle;
  border-collapse: collapse;
}

.patients-list_display_row:hover {
  background-color: #eeeeee;
  border-left: 3px solid #3398cc;
  -webkit-transition: 100ms;
  transition: 100ms;
}

.patients-list_display td, .patients-list_display th {
  text-align: left;
  border-bottom: solid #d9d9d9 1px;
  width: 33%;
  padding: 5px;
}

.patients-list_display th {
  color: #a8a8a8;
  font-weight: normal;
  padding: 10px 5px;
}
